
/* 만약 이 파일이 App.js 에만 적용되고 싶으면 파일이름을 'App.module.css' 라고 쓰면 됨 */





/* transition (전환 애니메이션) 주기 위해 동작 전, 동작 후 클래스 만듬 */

.start {
  opacity: 0;
}

.end {
  opacity: 1;
  /* transition을 0.5초에 걸쳐서 변경해줘. */
  transition: all 0.5s;
}





.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}




.cart {
  text-align: right;
}


.icon {
  float: right;
}






.logo {
  font-weight: bold;
  color: rgb(159, 104, 175);
}







.category:hover {
  font-weight: bold;
}

.login:hover {
  font-weight: bold;
}

.cart:hover {
  font-weight: bold;
}







.appImg {
